export const WithMobileScreen = {
  render: Template,
  name: 'With Small Screen (A5)',
  args: {
    ...defaultArgs,
  },
  parameters: {
    imageSnapshot: { pseudoStates: ['hover', 'focus-visible', 'active'] },
  },
  globals: {
    viewport: {
      value: '--breakpoint-xs',
    },
  },
} satisfies Story;

